package eu.karelhovorka.textgame.core

import eu.karelhovorka.textgame.core.bo.GameConfig
import eu.karelhovorka.textgame.core.bo.State
import eu.karelhovorka.textgame.core.bo.Move
import eu.karelhovorka.textgame.core.listener.*
import eu.karelhovorka.textgame.core.saver.LocalStorageGameSaver
import eu.karelhovorka.textgame.core.util.GameValidator
import kotlin.js.Json

data class GameJsonState(val id: String, val text: String, val movesIds: Array<String>, val flags: Array<String>)

data class GameJsonData(val startId: String, val states: Map<String, Any>, val moves: Map<String, Any>)


@JsName("getGameFromConfig")
fun getGameFromConfig(gameConfig: GameConfig): Game {
    val game = Game(gameConfig = gameConfig, listener = ListGameListener(arrayOf(ConsoleGameListener(), HtmlGameListener(), JsDelegateGameListener())), saver = LocalStorageGameSaver())
    val validator = GameValidator(gameConfig)
    validator.validate()
    return game
}

@JsName("loadGameFromConfig")
fun loadGameFromConfig(gameConfig: GameConfig): Game {
    val game = getGameFromConfig(gameConfig = gameConfig)
    game.load()
    return game
}
