package eu.karelhovorka.textgame.core.bot

import eu.karelhovorka.textgame.core.Game
import eu.karelhovorka.textgame.core.bo.Move
import eu.karelhovorka.textgame.core.util.random
import kotlinx.js.JsName

class GameBot(var initialGame: Game) {

    var crawlers = mutableListOf<GameCrawlContext>()

    val conditionFlags: Array<String>

    var visitedStates = mutableSetOf<String>()

    var visitedMoves = mutableSetOf<String>()

    init {
        val conditionList = mutableListOf<String>()
        for (move in initialGame.gameConfig.moves) {
            conditionList.addAll(move.flagsDisallowing)
            conditionList.addAll(move.flagsRequired)
        }
        conditionFlags = conditionList.toTypedArray()
    }

    var crawlersCount = 0

    @JsName("start")
    fun start():Int {
        val firstCrawler = GameCrawlContext(this, initialGame)
        addCrawler(firstCrawler)
        next()
        return crawlersCount
    }

    fun next() {
        if (crawlers.isNotEmpty()) {
            println("remaining: ${crawlers.size}/$crawlersCount")
            val crawler = crawlers.random()
            crawlers.remove(crawler)
            crawler.start()
        } else {
            println("crawling done, game played: $crawlersCount")
            checkVisitedAll()
        }
    }

    private fun checkVisitedAll() {
        initialGame.gameConfig.states
                .filterNot { visitedStates.contains(it.id) }
                //.forEach { throw IllegalStateException("State not visited at all: ${it.id}") }
        initialGame.gameConfig.moves
                .filterNot { visitedMoves.contains(it.id) }
        // we don't throw because we filter duplicates to reduce complexity
                //.forEach { throw IllegalStateException("Move not visited at all: ${it.id}") }

    }

    fun affectsConditions(move: Move): Boolean {
        for (flag in move.flags) {
            if (conditionFlags.contains(flag)) {
                return true
            }
        }
        return conditionFlags.contains(move.id)
    }

    private fun addCrawler(crawler: GameCrawlContext) {
        crawlers.add(crawler)
        crawlersCount += 1
    }

    fun cloneFromMove(parentContext: GameCrawlContext, move: Move) {
        val crawler = GameCrawlContext(this, parentContext.game, move)
        addCrawler(crawler)
    }

}

