package eu.karelhovorka.textgame.core

import eu.karelhovorka.textgame.core.bot.GameBot
import eu.karelhovorka.textgame.core.listener.*
import eu.karelhovorka.textgame.core.saver.GameSaver
import eu.karelhovorka.textgame.core.saver.LocalStorageGameSaver
import eu.karelhovorka.textgame.core.util.GameConverter
import eu.karelhovorka.textgame.core.util.GameValidator
import kotlinx.js.JsName

class GameLoader {

    private val saver_ = LocalStorageGameSaver()

    fun saver(): GameSaver {
        return saver_
    }

    @JsName("hasAutoSave")
    fun hasAutoSave(): Boolean {
        return saver().hasAutoSave()
    }

    @JsName("hasSave")
    fun hasSave(): Boolean {
        return saver().hasSave()
    }

    @JsName("autoload")
    fun autoload(gameText: String): Game {
        return load(gameText, GameSaver.AUTOSAVE)
    }

    @JsName("load")
    fun load(gameText: String): Game {
        return load(gameText, GameSaver.MAIN_SAVE)
    }

    private fun load(gameText: String, name: String): Game {
        val gameConfig = GameConverter().convert(gameText)
        if (!saver().has(name)) {
            throw IllegalStateException("Cannot load game $name!")
        }
        val gameState = saver().load(name)
        val game = Game(
                initialStateId = gameState.currentStateId,
                flags = gameState.flags.toMutableList(),
                gameConfig = gameConfig,
                listener = listener(),
                saver = saver()
        )
        val validator = GameValidator(gameConfig)
        validator.validate()
        return game

    }

    @JsName("newgame")
    fun newgame(gameText: String): Game {
        val gameConfig = GameConverter().convert(gameText)
        val game = Game(
                gameConfig = gameConfig,
                listener = listener(),
                saver = saver()
        )
        val validator = GameValidator(gameConfig)
        validator.validate()
        return game
    }

    @JsName("crawl")
    fun crawl(gameText: String): GameBot {
        val gameConfig = GameConverter().convert(gameText)
        val game = Game(
                gameConfig = gameConfig,
                listener = ListGameListener(arrayOf(
                        ConsoleGameListener()
                )),
                saver = saver()
        )
        val validator = GameValidator(gameConfig)
        validator.validate()
        return GameBot(game)
    }

    private fun listener(): GameListener {
        return ListGameListener(arrayOf(
                ConsoleGameListener(),
                HtmlGameListener(),
                JsDelegateGameListener(),
                AutosaveListener(saver()),
                CompletedGameListener(saver())
        ))
    }
}