package eu.karelhovorka.textgame.minigame

import kotlinx.js.JsName


enum class MinigameState {
    CREATED, RUNNING, FINISHED, PAUSED
}

abstract class Minigame(val id: String, private val listener: MinigameListener) {

    var state: MinigameState = MinigameState.CREATED

    lateinit var flags: List<String>

    @JsName("start")
    fun start(flags: List<String>) {
        if (state == MinigameState.RUNNING) {
            throw IllegalStateException("Game already running $id")
        }
        this.flags = flags
        onStarted()
        fireStart()
    }

    abstract fun onStarted()

    protected fun fireStart() {
        state = MinigameState.RUNNING
        listener.onStart(id)
    }

    protected fun firePause() {
        state = MinigameState.PAUSED
        fireEvent("pause", null)
    }

    protected fun fireUnpause() {
        state = MinigameState.RUNNING
        fireEvent("unpause", null)
    }


    protected fun fireFinish() {
        state = MinigameState.FINISHED
        listener.onFinish(id, flags.toTypedArray())
    }

    protected fun fireEvent(type: String, data: Any?) {
        listener.onEvent(MinigameEvent(id, type, data))
    }

}