package eu.karelhovorka.textgame.core.util

import eu.karelhovorka.textgame.core.bo.*


fun state(init: StateBuilder.() -> Unit): State {
    val stateBuilder = StateBuilder()
    stateBuilder.init()
    val state = stateBuilder.build()
    return state
}

fun move(init: MoveBuilder.() -> Unit): Move {
    val moveBuilder = MoveBuilder()
    moveBuilder.init()
    val move = moveBuilder.build()
    return move
}


fun game(init: GameConfigBuilder.() -> Unit): GameConfig {
    val gameconfig = GameConfigBuilder()
    gameconfig.init()
    return gameconfig.build()
}


data class StateBuilder(
        var id: String? = null,
        var illustration: String? = null,
        private var text_: String? = null,
        var flags: MutableList<String> = ArrayList(),
        var moveIds: MutableList<String> = ArrayList(),
        var chapterId: String? = null,
        var categories: Array<String> = emptyArray(),
        var endType: EndType = EndType.NONE,
        var theme: String? = null,
        var background: String? = null
) {
    var stringBuilder = CharSequenceBuilder()

    var text: CharSequence? = text_
        set(value) {
            if (value != null && value.contains("@")) {
                field = CharSequenceBuilder().appendFullText(value).build()
            } else {
                field = value
            }
        }


    fun build(): State {
        var finalText: CharSequence? = stringBuilder.build()
        if (text != null) {
            finalText = text
        }
        return State(
                id = id!!,
                illustration = illustration,
                text = finalText?.trim(),
                chapterId = chapterId!!,
                flags = flags.toTypedArray(),
                moveIds = moveIds.toTypedArray(),
                categories = categories,
                endType = endType,
                theme = theme,
                background = background
        )
    }

    fun text(init: CharSequenceBuilder.() -> Unit): CharSequenceBuilder {
        stringBuilder = CharSequenceBuilder()
        stringBuilder.init()
        return stringBuilder
    }

    fun appendText(value: String) {
        stringBuilder.appendLine(value)
    }

    fun appendMove(moveId: String) {
        moveIds.add(moveId)
    }

}

class CharSequenceBuilder {
    var strings = mutableListOf<String>()
    var text: String? = null

    fun build(): CharSequence? {
        if (strings.isEmpty()) {
            return text
        }
        if (text != null) {
            strings.add(text!!.trimEnd())
        }
        return MultiString(strings.toTypedArray())
    }

    fun appendFullText(value: CharSequence): CharSequenceBuilder {
        for (line in value.split("\n".toRegex())) {
            appendLine(line)
        }
        return this
    }

    fun appendLine(value: String) {
        if (value == "@") {
            strings.add(text!!.trimEnd())
            text = null
            return
        }
        if (text == null) {
            text = value + "\n"
        } else {
            text += value + "\n"
        }
    }
}

data class GameConfigBuilder(var startId: String? = null, var states: MutableList<State> = mutableListOf(), var moves: MutableList<Move> = mutableListOf(), var chapters: MutableList<Chapter> = mutableListOf()) {
    fun build(): GameConfig {
        return GameConfig(
                startId = startId!!,
                states = states.toTypedArray(),
                moves = moves.toTypedArray(),
                chapters = chapters.toTypedArray()
        )
    }

    fun state(init: StateBuilder.() -> Unit): State {
        val stateBuilder = StateBuilder(categories = emptyArray<String>())
        stateBuilder.init()
        val state = stateBuilder.build()
        states.add(state)
        return state
    }

    fun move(init: MoveBuilder.() -> Unit): Move {
        val moveBuilder = MoveBuilder()
        moveBuilder.init()
        val move = moveBuilder.build()
        moves.add(move)
        return move
    }

}

data class MoveBuilder(
        var id: String? = null,
        var caption: String? = null,
        var image: String? = null,
        var illustration: String? = null,
        var text: String? = null,
        var selfConsuming: Boolean = true,
        var targetId: String? = null,
        var flags: MutableList<String> = ArrayList(),
        var flagsRequired: MutableList<String> = ArrayList(),
        var flagsDisallowing: MutableList<String> = ArrayList(),
        var categories: Array<String> = emptyArray(),
        var theme: String? = null
) {
    fun build(): Move {
        if (id == null) {
            throw IllegalArgumentException("Id not found for move!")
        }
        if (targetId == null) {
            throw IllegalArgumentException("Target it not found for move ${id}!")
        }
        return Move(
                id = id!!,
                caption = caption,
                image = image,
                illustration = illustration,
                text = text?.trim(),
                selfConsuming = selfConsuming,
                targetId = targetId!!,
                flags = flags.toTypedArray(),
                flagsRequired = flagsRequired.toTypedArray(),
                flagsDisallowing = flagsDisallowing.toTypedArray(),
                categories = categories,
                theme = theme
        )
    }

    fun addFlag(value: String) {
        flags.add(value)
    }

    fun appendText(value: String) {
        if (text == null) {
            text = value + "\n"
        } else {
            text += value + "\n"
        }
    }

    fun disallowFlag(flagDisallowing: String) {
        flagsDisallowing.add(flagDisallowing)
    }

    fun requireFlag(flagRequiring: String) {
        flagsRequired.add(flagRequiring)
    }
}

data class ChapterBuilder(
        val id: String,
        var name: String? = null,
        var categories: Array<String> = emptyArray(),
        var theme: String? = null
) {
    fun build(): Chapter {
        if (name != null) {
            return Chapter(id = id, name = name!!, categories = categories, theme = theme)
        }
        throw IllegalArgumentException("Name not found for chapter: $id")

    }

    fun name(value: String) {
        name = value
    }
}