package eu.karelhovorka.textgame.minigame

class ConsoleMinigameListener: GameLoopMinigameListener {
    override fun onLoop(delta: Double) {
        println("onLoop(delta: $delta)")
    }

    override fun onStart(id: String) {
        println("onStart(id: $id)")
    }

    override fun onFinish(id: String, flags: Array<String>) {
        println("onFinish(id: $id, flags: $flags)")
    }

    override fun onEvent(event: MinigameEvent) {
        println("onEvent(event: $event)")
    }

}