package eu.karelhovorka.textgame.minigame

import kotlinx.js.JsName


data class Options(val path: String, val loop: Boolean = true, val volume: Int = 0) {

}

data class SoundOptions(val source: String = "file", val options: Options) {

}

data class AudioTrack(val fileName: String, val loop: Boolean = false, var volume: Int = 0) {
    var sound: dynamic = null
    var distortion: dynamic = null
    var delay: dynamic = null

    init {
        distortion = js("new Pizzicato.Effects.Distortion({\n" +
                "    gain: 0.0\n" +
                "});")
        delay = js("new Pizzicato.Effects.Delay({\n" +
                "    feedback: 0.6,\n" +
                "    time: 0.333,\n" +
                "    mix: 0.5\n" +
                "});")
    }

    fun load() {
        val so = soundOptions()
        sound = js("new Pizzicato.Sound(so)")
        sound.addEffect(distortion)
        sound.addEffect(delay)
    }

    private fun soundOptions(): SoundOptions {
        return SoundOptions(options = Options(path = "./audio/$fileName", loop = loop, volume = volume))
    }

    fun toggleDistortion() {
        if (distortion.gain == 0) {
            distortion.gain = 0.4
        } else {
            distortion.gain = 0
        }
    }

    fun toggleDelay() {
        if (delay.feedback > 0) {
            delay.feedback = 0
            delay.time = 0
            delay.min = 0
        } else {
            delay.feedback = 0.6
            delay.time = 0.333
            delay.min = 0.5
        }
    }

}

data class TrackGroup(val tracks: Array<AudioTrack>, var currentIndex: Int = 0) {

    init {
        tracks.forEach {
            it.volume = 0
        }
        currentTrack().volume = 1
    }

    fun load() {
        tracks.forEach { it.load(); }
    }

    private fun currentTrack(): AudioTrack {
        return tracks[currentIndex]
    }

    fun switchTrack() {
        currentTrack().sound.volume = 0
        currentTrack().volume = 0
        currentIndex = (currentIndex + 1 + tracks.size) % tracks.size
        currentTrack().volume = 1
        currentTrack().sound.volume = 1
        println(currentTrack())
        println("currentTrack: ${currentIndex}")
    }

    fun toggleDistortion() {
        currentTrack().toggleDistortion()
    }

    fun toggleDelay() {
        currentTrack().toggleDelay()
    }
}

class SoundController() {
    var group: dynamic = null
    val solo = TrackGroup(arrayOf(
            AudioTrack("melodie1_cista.ogg"),
            AudioTrack("melodie2.ogg"),
            AudioTrack("melodie3.ogg")
    ))

    val rhythm = TrackGroup(arrayOf(
            AudioTrack("doprovod_cisty.ogg", loop = true),
            AudioTrack("doprovod_delay.ogg", loop = true),
            AudioTrack("doprovod_sekany_akordy.ogg", loop = true)
    ))

    val trackGroups = mutableMapOf(
            "rhythm" to rhythm,
            "solo" to solo
    )

    @JsName("switchSolo")
    fun switchSolo() {
        solo.switchTrack()
    }

    @JsName("switchRhythm")
    fun switchRhythm() {
        rhythm.switchTrack()
    }

    @JsName("toggleSoloDistortion")
    fun toggleSoloDistortion() {
        solo.toggleDistortion()
    }

    @JsName("toggleRhythmDistortion")
    fun toggleRhythmDistortion() {
        rhythm.toggleDistortion()
    }

    @JsName("toggleSoloDelay")
    fun toggleSoloDelay() {
        solo.toggleDelay()
    }

    @JsName("toggleRhythmDelay")
    fun toggleRhythmDelay() {
        rhythm.toggleDelay()
    }

    @JsName("trackGroupArray")
    fun trackGroupArray(): Array<TrackGroup> {
        return trackGroups.values.toTypedArray()
    }

    @JsName("sounds")
    fun sounds(): Array<dynamic> {
        val list = mutableListOf<dynamic>()
        trackGroups.values.forEach {
            it.tracks.forEach { s -> list.add(s.sound) }
        }
        return list.toTypedArray()
    }

    @JsName("load")
    fun load() {
        trackGroups.values.forEach { it.load() }
        val list = sounds()
        group = js("new Pizzicato.Group(list)")
    }

}