package eu.karelhovorka.textgame.core.listener

import eu.karelhovorka.textgame.core.Game
import eu.karelhovorka.textgame.core.GameListener
import eu.karelhovorka.textgame.core.bo.EndType
import eu.karelhovorka.textgame.core.bo.Move
import eu.karelhovorka.textgame.core.bo.State
import eu.karelhovorka.textgame.core.bo.isInteractive

//fun <K,V> Map<K,V>.getOrThrow(key: K): V {

open class ConsoleGameListener : GameListener {
    override fun onRestart(game: Game) {
        println("restart")
    }

    override fun onPause() {
        println("PAUSE")
    }

    lateinit var game: Game

    override fun onState(state: State, moves: Array<Move>) {
        if (state.text != null) {
            println("----")
            println(state.text)
        }
        if (moves.isInteractive()) {
            println("----")
            for (index in moves.indices) {
                val move = moves[index]
                println("$index - ${move.id} - ${move.caption}")
            }
            expectAnswer(state, moves)
        } else {
            requireKey()
        }
        println("----")
    }

    open fun requireKey() {
        //println("----Enter pro pokračování----")

    }

    open fun expectAnswer(state: State, moves: Array<Move>) {
    }

    override fun onMove(move: Move) {
        if (move.caption != null) {
            println("${move.id} ${move.caption}")
        }
        if (move.text != null) {
            println("${move.text}")
        }
        if (move.caption == null && move.text == null) {
            println("Invisible move: ${move.id}")
        }
    }

    override fun onStart(game: Game) {
        println("--START--")
        this.game = game
    }

    override fun onFinish(game: Game, endType: EndType) {
        println("--KONEC--")
    }
}
