package eu.karelhovorka.textgame.core.saver

import eu.karelhovorka.textgame.core.bo.GameCompletion
import eu.karelhovorka.textgame.core.bo.GameState
import org.w3c.dom.get
import kotlin.browser.localStorage

class LocalStorageGameSaver : GameSaver() {

    override fun save(gameCompletion: GameCompletion) {
        localStorage.setItem(VISITED, JSON.stringify(gameCompletion))
    }

    override fun loadCompletion(): GameCompletion {
        val text = localStorage.getItem(VISITED) ?: return GameCompletion()
        val gson: dynamic = JSON.parse(text)
        return GameCompletion(
                stateIds = gson["stateIds"],
                moveIds = gson["moveIds"]
        )
    }

    override fun has(name: String): Boolean {
        return localStorage.getItem(name) != null
    }

    override fun save(name: String, game: GameState) {
        val saved = JSON.stringify(game)
        localStorage.setItem(name, saved)
    }

    override fun load(name: String): GameState {
        val gson: dynamic = JSON.parse(localStorage.getItem(name)!!)
        val currentStateId = gson["currentStateId"]
        val flags: Array<String> = gson["flags"]
        return GameState(
                currentStateId = currentStateId,
                flags = flags
        )
    }
}