package eu.karelhovorka.textgame.minigame


abstract class AbstractGameLoopBeat(val game: GameLoopMinigame) {

    var started = false

    fun start() {
        started = true
        onStart()
    }

    fun stop() {
        started = false
    }

    abstract fun onStart()


}

expect class GameLoopBeat(game: GameLoopMinigame): AbstractGameLoopBeat {


}