package eu.karelhovorka.textgame.core.util

import eu.karelhovorka.textgame.core.Game
import eu.karelhovorka.textgame.core.bo.GameConfig
import eu.karelhovorka.textgame.core.bo.Move
import eu.karelhovorka.textgame.core.bo.State
import eu.karelhovorka.textgame.core.listener.EmptyListener


val STATE_ID_REGEX = "u[0-9A-Za-z_]*".toRegex()
val MOVE_ID_REGEX = "h[0-9A-Za-z_]*".toRegex()

fun getFlags(gameConfig: GameConfig): Array<String> {
    val flags = ArrayList<String>()
    for (move in gameConfig.moves) {
        flags.add(move.id)
        flags.addAll(move.flags)
    }
    for (state in gameConfig.states) {
        flags.add(state.id)
        flags.addAll(state.flags)
    }
    return flags.toTypedArray()
}

class GameValidator(val gameConfig: GameConfig) {
    val helper = GameConfigHelper(gameConfig)

    var flags = getFlags(gameConfig)

    fun validateStates() {
        validateNotDuplicitStateIds()
        for (state in gameConfig.states) {
            validateState(state)
        }
    }

    fun validateNotDuplicitStateIds() {
        val duplicitIds = mutableListOf<String>()
        for (state in gameConfig.states) {
            if (duplicitIds.contains(state.id)) {
                throw IllegalStateException("State ${state.id} already in text!\n KIKI to znamená, že je několikrát v textu tohle id, nejspíš copy-paste překlep.")
            }
            duplicitIds.add(state.id)
        }
    }

    private fun validateState(state: State) {
        if (!STATE_ID_REGEX.matches(state.id)) {
            println("Invalid state id: ${state.id} \nKIKI, má přece začínat na 'u'!")
            throw IllegalStateException("Invalid state id: ${state.id} \nKIKI, má přece začínat na 'u'!")
        }
        validateStateMovesExist(state)
        validateMovesNotAmbiguous(state)
        validateMovesAllHaveImageOrNot(state)
    }

    private fun validateMovesNotAmbiguous(state: State) {
        val moves = helper.movesOf(state)
        val autoMoveNoRestrictionMoves = moves.filter { m -> m.isAutoMove() && m.flagsDisallowing.isEmpty() && m.flagsRequired.isEmpty() }
        if (autoMoveNoRestrictionMoves.size > 1) {
            throw IllegalStateException("Multiple automoves with no flag restrictions for state ${state.id} \nKIKI to znamená, že je tam víc tahů, který nemaj popisek. Buď musí mít všechny krom 1 popisek, nebo musí bejt pošéfovaný pomocí podminena/vyloucena, aby vždycky takovej byl maximálně jeden, ten 'automatickej'")
        }
    }

    private fun validateMovesAllHaveImageOrNot(state: State) {
        val moves = helper.movesOf(state)
        val movesWithoutImages = moves.filter { it.image == null }
        val movesWithImages = moves.filter { it.image != null }
        if (movesWithImages.isNotEmpty() && movesWithoutImages.isNotEmpty()) {
            throw IllegalStateException("Some moves of state ${state.id} have images and some don't. \nKIKI to znamená, že některý tahy tohodle stavu maj nastavenej obrázek a některý nemaj!")
        }
    }

    private fun validateStateMovesExist(state: State) {
        for (moveId in state.moveIds) {
            try {
                helper.moveById(moveId)
            } catch (e: Exception) {
                println("Cannot find move $moveId from state ${state.id}")
                throw IllegalStateException("Cannot find move $moveId from state ${state.id}")
            }
        }
    }


    fun validateMoves(ignoreAcrossChapters: Boolean = true) {
        validateNotDuplicitMoveIds()
        for (move in gameConfig.moves) {
            validateMove(move, ignoreAcrossChapters = ignoreAcrossChapters)
        }
    }

    fun validateNotDuplicitMoveIds() {
        val duplicitIds = mutableListOf<String>()
        for (move in gameConfig.moves) {
            if (duplicitIds.contains(move.id)) {
                throw IllegalStateException("Move ${move.id} already in text!\n KIKI to znamená, že je několikrát v textu tohle id hrany, nejspíš copy-paste překlep.")
            }
            duplicitIds.add(move.id)
        }
    }

    private fun validateMove(move: Move, ignoreAcrossChapters: Boolean = true) {
        if (!MOVE_ID_REGEX.matches(move.id)) {
            println("Invalid move id: ${move.id} \nKIKI to znamená, že tah musí začínat na 'h' \n")
            throw IllegalStateException("Invalid move id: ${move.id} \nKIKI to znamená, že tah musí začínat na 'h'")
        }
        try {
            helper.stateById(move.targetId)
        } catch (e: Exception) {
            if (move.acrossChapters() && ignoreAcrossChapters) {
                //it's move across chapters, so we hope it is just move to next chapter
            } else {
                println("Cannot find target ${move.targetId} from move ${move.id} \nKIKI to znamená, že hrana '${move.id}' má špatně nastavenej 'cil'")
                throw IllegalStateException(("Cannot find target ${move.targetId} from move ${move.id} \nKIKI to znamená, že hrana '${move.id}' má špatně nastavenej 'cil'"))
            }
        }
        for (flag in move.flagsDisallowing) {
            if (!(flag in flags)) {
                throw IllegalStateException("Non-existent flagsDisallowing $flag for move ${move.id} \nKIKI to znamená, že ta vlajka neexistuje - neni nikde nastavená pomocí příznaku, ani to neni existující tah/uzel. Jo a oddělujou se čárkou!")
            }
        }
        for (flag in move.flagsRequired) {
            if (!(flag in flags)) {
                throw IllegalStateException("Non-existent flagsRequired $flag for move ${move.id} \n" +
                        "KIKI to znamená, že ta vlajka neexistuje - neni nikde nastavená pomocí příznaku, ani to neni existující tah/uzel. Jo a oddělujou se čárkou!")
            }
        }
    }

    fun validate() {
        validateMoves()
        validateStates()
    }

}