package eu.karelhovorka.textgame.minigame

import kotlinx.js.JsName

class GameRepository(var listener: GameLoopMinigameListener = ConsoleMinigameListener(), private val games: MutableMap<String, (GameLoopMinigameListener) -> Minigame> = mutableMapOf()) {

    init {
        games[UnlockMiniGame.ID] = ::UnlockMiniGame
    }

    @JsName("createGame")
    fun createGame(id: String, listener: GameLoopMinigameListener): Minigame {
        return games[id]!!(listener)
    }
}