package eu.karelhovorka.textgame.core.util

import eu.karelhovorka.textgame.core.bo.GameConfig
import eu.karelhovorka.textgame.core.bo.Move
import eu.karelhovorka.textgame.core.bo.State

class GameConfigHelper(val gameConfig: GameConfig) {
    private val statesById = gameConfig.states.associate { state -> state.id to state }
    private val movesById = gameConfig.moves.associate { move -> move.id to move }

    fun stateById(id: String): State {
        return statesById.getOrThrow(id)
    }

    fun start(): State {
        return stateById(gameConfig.startId)
    }

    fun moveById(id: String): Move {
        return movesById.getOrThrow(id)
    }

    fun movesOf(state: State): Array<Move> {
        val moves: MutableList<Move> = ArrayList()
        for (moveId in state.moveIds) {
            val move = movesById.getOrThrow(moveId)
            moves.add(move)
        }
        return moves.toTypedArray()
    }

    fun containsState(stateId: String): Boolean {
        for (state in gameConfig.states) {
            if (state.id == stateId) {
                return true
            }
        }
        return false
    }

    fun moveExists(from: String, to: String): Boolean {
        if (!statesById.containsKey(from)) {
            return false
        }
        val state = statesById.getOrThrow(from)

        for (moveId in state.moveIds) {
            if (movesById.containsKey(moveId)) {
                val move = movesById.getOrThrow(moveId)
                if (move.targetId == to) {
                    return true
                }
            }
        }
        return false
    }

    fun pathExists(from: String, to: String): Boolean {
        if (from == to) {
            return true
        }
        if (to == gameConfig.startId) {
            return false
        }
        val movesByTargetId = gameConfig.moves.associate { move -> move.targetId to move }
        val move = movesByTargetId.getOrThrow(to)
        for (state in gameConfig.states) {
            for (moveId in state.moveIds) {
                if (moveId == move.id) {
                    return pathExists(from, state.id)
                }
            }
        }
        throw IllegalArgumentException("No parent found for move ${move.id}")
    }
}