package eu.karelhovorka.textgame.core.listener

import eu.karelhovorka.textgame.core.Game
import eu.karelhovorka.textgame.core.GameListener
import eu.karelhovorka.textgame.core.bo.EndType
import eu.karelhovorka.textgame.core.bo.Move
import eu.karelhovorka.textgame.core.bo.State
import eu.karelhovorka.textgame.core.bo.isInteractive
import eu.karelhovorka.textgame.core.common.RandomUtil
import org.w3c.dom.*
import kotlin.browser.document


fun CharSequence.toHtml(): String {
    return textToHtml(this.toString())
}

fun textToHtml(text: String): String {
    var result = ""
    for (line in text.split("\n".toRegex())) {
        result += "<p>$line</p>"
    }
    return result
}

class HtmlGameListener : GameListener {
    override fun onRestart(game: Game) {
        gameDiv.innerHTML = ""
    }

    override fun onPause() {
        gameDiv.appendChild(continueElement())
    }

    fun hideAllContinues() {
        val previousStates: NodeList = document.querySelectorAll(".continue")
        if (previousStates.length > 0) {
            val previousState = previousStates.item(previousStates.length - 1) as HTMLDivElement
            previousState.classList.add("finished")
        }
    }

    var gameDiv: HTMLDivElement = document.getElementById("game") as HTMLDivElement

    var themeDiv: HTMLDivElement = document.getElementById("theme") as HTMLDivElement

    override fun onState(state: State, moves: Array<Move>) {
        hideAllContinues()
        if (state.theme != null) {
            themeDiv.className = state.theme!!
        }
        val element = stateToElement(state)
        state.categories.forEach { element.classList.add(it) }
        gameDiv.appendChild(element)
        //element.scrollIntoView()
        if (moves.isInteractive()) {
            val moveElement = movesToElement(moves)
            state.categories.forEach { moveElement.classList.add(it) }
            moveElement.classList.add("moves-" + state.id)
            gameDiv.appendChild(moveElement)
        }
    }

    private fun continueElement(): Element {
        val movesWrap = document.createElement("div")
        movesWrap.classList.add("continue")
        movesWrap.classList.add("version-a-" + (RandomUtil.nextInt(10) + 1))
        movesWrap.classList.add("version-b-" + (RandomUtil.nextInt(6) + 1))
        movesWrap.innerHTML = "<span><a href='javascript:window.game.continueMove()'>Pokračovat</a></span>"
        return movesWrap
    }

    private fun movesToElement(moves: Array<Move>): Element {
        val movesWrap = document.createElement("ul")
        movesWrap.classList.add("moves")
        for (move in moves) {
            val moveElement = document.createElement("li")
            move.categories.forEach { moveElement.classList.add(it) }
            moveElement.classList.add("choice-" + move.id)
            moveElement.classList.add("version-a-" + (RandomUtil.nextInt(5) + 1))
            moveElement.classList.add("version-b-" + (RandomUtil.nextInt(5) + 1))
            if (move.image == null) {
                movesWrap.classList.add("no-images")
                moveElement.innerHTML = "<span class='helper-1'></span><a href='javascript:window.game.move(\"${move.id}\")'><span class='helper-2'></span>${move.caption}</a>"
            } else {
                movesWrap.classList.add("has-images")
                moveElement.innerHTML = "<span class='helper-1'></span><a href='javascript:window.game.move(\"${move.id}\")'><span class='helper-2'></span><img title='${move.caption}' src='img/moves/${move.image}' /><div class='caption'>${move.caption}</div></a>"
            }
            movesWrap.appendChild(moveElement)
        }
        return movesWrap
    }

    private fun hidePreviousStates() {
        val previousStates: NodeList = document.querySelectorAll(".state")
        if (previousStates.length > 0) {
            val previousState = previousStates.item(previousStates.length - 1) as HTMLDivElement
            previousState.classList.add("finished")
        }
    }

    fun stateToElement(state: State): Element {
        hidePreviousStates()
        val element = document.createElement("div")
        element.classList.add("state")
        element.classList.add(state.id)
        if (state.text == null) {
            element.classList.add("no-text")
        } else {
            element.classList.add("has-text")
        }
        if (state.illustration == null) {
            element.classList.add("no-illustration")
        } else {
            element.classList.add("has-illustration")
        }
        val text = state.text?.toHtml() ?: ""
        if (state.illustration == null) {
            element.innerHTML = "<span class='helper-1'></span><div class='text'>${text}</div>"
        } else {
            element.innerHTML = "<span class='helper-1'></span><div class='text'><img class='illustration' src='img/illustrations/${state.illustration}' />${text}</div>"
        }
        return element
    }

    fun scrollToBottom() {
        js("window.scrollTo(0,document.body.scrollHeight)")
    }


    override fun onMove(move: Move) {
        hideAllContinues()
        if (move.theme != null) {
            themeDiv.className = move.theme!!
        }
        if (move.text != null) {
            val element = moveToElement(move)
            gameDiv.appendChild(element)
        }
        val liElements: NodeList = document.querySelectorAll(".choice-" + move.id);
        if (liElements.length == 0) {
            return
        }
        val li = liElements.item(liElements.length - 1) as HTMLLIElement
        li.classList.add("selected");
        li.parentElement!!.classList.add("finished");
        (li.parentElement!!.previousSibling as HTMLElement).classList.add("finished");
    }

    private fun moveToElement(move: Move): Element {
        val element = document.createElement("div")
        move.categories.forEach { element.classList.add(it) }
        element.classList.add("move")
        element.classList.add(move.id)
        if (move.text != null) {
            element.innerHTML = "<div class='text'>${move.text?.toHtml()}</div>"
        }
        return element
    }

    override fun onStart(game: Game) {
    }

    override fun onFinish(game: Game, endType: EndType) {
        val finish = document.createElement("div")
        finish.classList.add("finish")
        finish.innerHTML = "KONEC"
        gameDiv.appendChild(finish)
    }
}
