package eu.karelhovorka.textgame.core.saver

import eu.karelhovorka.textgame.core.bo.GameCompletion
import eu.karelhovorka.textgame.core.bo.GameState
import eu.karelhovorka.textgame.core.util.getOrThrow

class MemoryGameSaver : GameSaver() {
    private var gameCompletion = GameCompletion()

    override fun save(gameCompletion: GameCompletion) {
        this.gameCompletion = gameCompletion
    }

    override fun loadCompletion(): GameCompletion {
        TODO("not implemented") //To change body of created functions use File | Settings | File Templates.
    }

    private val map: MutableMap<String, GameState> = mutableMapOf()

    override fun save(name: String, game: GameState) {
        map.put(name, game)
    }

    override fun load(name: String): GameState {
        return map.getOrThrow(name)
    }

    override fun has(name: String): Boolean {
        return map.contains(name)
    }
}