package eu.karelhovorka.textgame.core.listener.stack

import eu.karelhovorka.textgame.core.Game
import eu.karelhovorka.textgame.core.GameListener
import eu.karelhovorka.textgame.core.bo.EndType
import eu.karelhovorka.textgame.core.bo.Move
import eu.karelhovorka.textgame.core.bo.State
import eu.karelhovorka.textgame.core.bo.isInteractive


data class Event(val name: String, val data: Any, val data2: Any? = null) {

}


class GameListenerDelegate(val listener: GameListener) : GameListener {

    var paused = false
    val eventStack = mutableListOf<Event>()

    override fun onPause() {
        throw IllegalStateException("Should not be called directly")
    }

    override fun onRestart(game: Game) {
        eventStack.clear()
        paused = false
        listener.onRestart(game)
    }

    override fun onState(state: State, moves: Array<Move>) {
        if (eventStack.isEmpty() && !paused) {
            listener.onState(state, moves)
        } else {
            eventStack.add(Event("onState", state, moves))
        }
        if (!state.isAutoMove() && !moves.isInteractive()) {
            pauseIfNotPaused()
        }
    }

    override fun onMove(move: Move) {
        if (eventStack.isEmpty() && !paused) {
            listener.onMove(move)
            if (move.text != null) {
                pauseIfNotPaused()
            }
        } else {
            if (!move.isAutoMove()) {
                eventStack.add(Event("onMove", move))
                pauseIfNotPaused()
            }
        }
    }

    private fun pauseIfNotPaused() {
        if (!paused) {
            paused = true
            listener.onPause()
        }
    }

    override fun onStart(game: Game) {
        listener.onStart(game)
    }

    override fun onFinish(game: Game, endType: EndType) {
        if (eventStack.isEmpty()) {
            paused = false
            listener.onFinish(game, endType)
        } else {
            eventStack.add(Event("onFinish", game, endType))
            pauseIfNotPaused()
        }
    }

    fun nextEvent() {
        if (eventStack.isEmpty()) {
            throw IllegalStateException("EventStack is empty!")
        }
        val first = eventStack.removeAt(0)
        if (first.name == "onState") {
            val state = first.data as State
            val moves: Array<Move> = (first.data2 as Array<Move>?)!!
            listener.onState(state, moves)
        } else if (first.name == "onMove") {
            listener.onMove(first.data as Move)
        } else if (first.name == "onFinish") {
            val endType = first.data2 as EndType
            listener.onFinish(first.data as Game, endType)
        } else if (first.name == "onStart") {
            listener.onStart(first.data as Game)
        } else {
            throw IllegalArgumentException("Invalid name: ${first.name}")
        }
        paused = false
        if (eventStack.size == 1 && eventStack.first().name == "onFinish") {
            val second = eventStack.removeAt(0)
            val endType = second.data2 as EndType
            val game = second.data as Game
            listener.onFinish(game, endType)
        }
        if (eventStack.isNotEmpty()) {
            pauseIfNotPaused()
        }
    }
}
