package eu.karelhovorka.textgame.core.saver

import eu.karelhovorka.textgame.core.bo.GameCompletion
import eu.karelhovorka.textgame.core.bo.GameState
import kotlinx.js.JsName

abstract class GameSaver {

    abstract fun save(name: String, game: GameState)
    abstract fun load(name: String): GameState
    abstract fun has(name: String): Boolean

    abstract fun save(gameCompletion: GameCompletion)
    abstract fun loadCompletion(): GameCompletion

    @JsName("save")
    fun save(game: GameState) {
        save(MAIN_SAVE, game)
    }

    @JsName("load")
    fun load(): GameState {
        return load(MAIN_SAVE)
    }

    @JsName("hasSave")
    fun hasSave(): Boolean {
        return has(MAIN_SAVE)
    }

    @JsName("autosave")
    fun autosave(game: GameState) {
        save(AUTOSAVE, game)
    }

    @JsName("autoload")
    fun autoload(): GameState {
        return load(AUTOSAVE)
    }

    @JsName("hasAutoSave")
    fun hasAutoSave(): Boolean {
        return has(AUTOSAVE)
    }

    companion object {
        val VISITED = "VISITED"
        val AUTOSAVE = "AUTOSAVE"
        val MAIN_SAVE = "SAVE"
    }

}