package eu.karelhovorka.textgame.minigame

import kotlin.browser.window

actual class GameLoopBeat actual constructor(game: GameLoopMinigame) : AbstractGameLoopBeat(game) {

    var lastTimestamp: Double = (-1).toDouble()

    fun frame(timestamp: Double) {
        if (!started) {
            return
        }
        if (lastTimestamp == (-1).toDouble()) {
            lastTimestamp = timestamp
            window.requestAnimationFrame { ts -> frame(ts) }
            return
        }
        val delta = timestamp - lastTimestamp
        lastTimestamp = timestamp
        game.loop(delta)
        window.requestAnimationFrame { ts -> frame(ts) }
    }

    override fun onStart() {
        window.requestAnimationFrame { ts -> frame(ts) }
    }


}