package eu.karelhovorka.textgame.core

import eu.karelhovorka.textgame.core.bo.*
import eu.karelhovorka.textgame.core.saver.GameSaver
import eu.karelhovorka.textgame.core.saver.MemoryGameSaver
import eu.karelhovorka.textgame.core.util.getOrThrow
import eu.karelhovorka.textgame.core.listener.stack.GameListenerDelegate
import eu.karelhovorka.textgame.core.util.GameConfigHelper

typealias JsName = kotlinx.js.JsName

class Game(
        val flags: MutableList<String> = mutableListOf(),
        val gameConfig: GameConfig, listener: GameListener,
        var initialStateId: String? = null,
        val usePause: Boolean = true,
        val saver: GameSaver = MemoryGameSaver()
) {
    val gameConfigHelper = GameConfigHelper(gameConfig)
    var currentState: State
    val TESTING = TestingGame()
    val listener: GameListener

    init {
        if (usePause) {
            this.listener = GameListenerDelegate(listener)
        } else {
            this.listener = listener
        }
        currentState = initState()
    }

    private fun initState(): State {
        return if (initialStateId == null) {
            gameConfigHelper.stateById(gameConfig.startId)
        } else {
            gameConfigHelper.stateById(initialStateId!!)
        }
    }

    @JsName("start")
    fun start() {
        listener.onStart(this)
        this.visitState(initState())
    }

    @JsName("restart")
    fun restart() {
        this.flags.clear()
        listener.onRestart(this)
        start()
    }

    @JsName("save")
    fun save(): GameState {
        val gameState = state()
        saver.save(gameState)
        return gameState
    }

    fun state(): GameState {
        return GameState(
                flags = flags.toTypedArray(),
                currentStateId = currentState.id
        )
    }

    @JsName("load")
    fun load(): Game {
        val gameState: GameState = saver.load()
        this.initialStateId = gameState.currentStateId
        this.currentState = gameConfigHelper.stateById(gameState.currentStateId)
        this.flags.clear()
        this.flags.addAll(gameState.flags)
        println("loaded")
        return this
    }

    private fun visitState(state: State) {
        this.currentState = state
        flags.add(currentState.id)
        flags.addAll(currentState.flags)
        //statesVisited.add(currentState.id)
        val moves = filterMoves(this.currentState)
        listener.onState(currentState, moves)
        if (moves.isEmpty()) {
            if (state.endType == EndType.NONE) {
                throw IllegalStateException("Just about to end with state '${state.id}' and no explicit endingType")
            }
            listener.onFinish(this, state.endType)
        } else {
            if (state.endType != EndType.NONE) {
                throw IllegalStateException("State has explicit ending type, but moves out: '${state.id}'")
            }
        }
        if (moves.size == 1 && moves[0].isAutoMove()) {
            val move = moves[0]
            move(move.id)
        }
    }


    @JsName("move")
    fun move(moveId: String) {
        //TODO: filtered moves instead
        if (!currentState.moveIds.contains(moveId)) {
            throw IllegalStateException("Move $moveId not found in state: ${currentState.id}")
        }
        move(gameConfigHelper.moveById(moveId))
    }

    @JsName("continueMove")
    fun continueMove() {
        if (listener is GameListenerDelegate) {
            listener.nextEvent()
        } else {
            throw IllegalStateException("Cannot continue when pausing is disabled")
        }

    }

    fun move(move: Move) {
        this.flags.add(move.id)
        this.flags.addAll(move.flags)
        //this.movesUsed.add(move.id)
        listener.onMove(move)
        if (!gameConfigHelper.containsState(move.targetId) && move.acrossChapters()) {
            listener.onFinish(this, EndType.UNFINISHED)
            return
        }
        val newState = gameConfigHelper.stateById(move.targetId)
        this.visitState(newState)
    }

    internal fun filterMoves(state: State): Array<Move> {
        val moves: MutableList<Move> = ArrayList()
        for (moveId in state.moveIds) {
            val move = gameConfigHelper.moveById(moveId)
            if (canMove(move)) {
                moves.add(move)
            }
        }
        moves.checkMultipleNonInteractive()
        if (moves.isInteractive()) {
            return moves.removeNotInteractive().toTypedArray()
        }
        return moves.toTypedArray()
    }

    fun canMove(move: Move): Boolean {
        if (move.selfConsuming && flags.contains(move.id)) {
            return false
        }
        var foundFlags: Int = 0
        for (flag in move.flagsDisallowing) {
            if (flags.contains(flag)) {
                foundFlags += 1
            }
        }
        if (move.flagsDisallowing.isNotEmpty() && foundFlags == move.flagsDisallowing.size) {
            return false
        }
        for (flag in move.flagsRequired) {
            if (!flags.contains(flag)) {
                return false
            }
        }
        return true
    }

    @JsName("completed")
    fun completed(): Float {
        return eu.karelhovorka.textgame.core.gameCompleted(gameCompletion = saver.loadCompletion(), config = gameConfig)
    }


    inner class TestingGame {

        @JsName("loadState")
        fun loadState(stateId: String) {
            visitState(gameConfigHelper.stateById(stateId))
        }

        @JsName("clearFlags")
        fun clearFlags() {
            flags.clear()
        }

    }
}


fun gameCompleted(gameCompletion: GameCompletion, config: GameConfig): Float {
    return (gameCompletion.moveIds.size + gameCompletion.stateIds.size).toFloat() / (config.states.size + config.moves.size).toFloat()
}
