package eu.karelhovorka.textgame.minigame

import kotlin.math.sin

class UnlockMiniGame(
        listener: GameLoopMinigameListener,
        val range: IntRange = IntRange(0, 100),
        var speedValueS: Double = 10.0,
        var currentPosition: Double = ((range.endInclusive - range.start).toDouble() / 2.0)
) : GameLoopMinigame(ID, listener) {

    constructor(listener: GameLoopMinigameListener) : this(listener, IntRange(0, 100)) {

    }

    var progress = 0.0

    override fun doLoop(delta: Double) {
        progress += speedValueS * delta
        currentPosition = sin(progress / 1000.0)
        fireEvent("position", currentPosition)
    }

    override fun onStarted() {
        super.onStarted()
    }

    companion object {
        val ID = "unlocking"

        fun create(listener: GameLoopMinigameListener): Minigame {
            return UnlockMiniGame(listener)
        }
    }

}