package eu.karelhovorka.textgame.minigame

import kotlinx.js.JsName

abstract class GameLoopMinigame(id: String, var listener: GameLoopMinigameListener) : Minigame(id, listener) {

    val beat: GameLoopBeat = GameLoopBeat(this)


    override fun onStarted() {
        beat.start()
    }

    fun loop(delta: Double) {
        if (state != MinigameState.RUNNING) {
            return
        }
        doLoop(delta)
        listener.onLoop(delta)
    }

    @JsName("kill")
    fun kill() {
        flags = listOf("killed")
        beat.stop()
        fireFinish()
    }

    protected abstract fun doLoop(delta: Double)
}