package eu.karelhovorka.textgame.core.util

import eu.karelhovorka.textgame.core.bo.*
import eu.karelhovorka.textgame.core.common.RandomUtil


object SmartRandom {
    val lastRandomMap = mutableMapOf<Int, Int>()

    fun nextInt(max: Int): Int {
        var value = RandomUtil.nextInt(max)
        val lastValue = lastRandomMap.get(max)
        if (lastValue != null && lastValue == value) {
            value = RandomUtil.nextInt(max)
        }
        lastRandomMap.put(max, value)
        return value
    }
}

typealias JsName = kotlinx.js.JsName

val NODE_SELF_CONSUMING = false

data class SuperStateBuilder(val id: String, var currentTheme: String? = null, var selfConsuming: Boolean = NODE_SELF_CONSUMING, var finalTargetId: String? = null, var chapterId: String? = null, var tmpStates: MutableList<StateBuilder> = mutableListOf()) {

    init {
        addState()
    }

    fun build(): Pair<List<State>, List<Move>> {
        if (finalTargetId == null && currentStateBuilder().moveIds.isEmpty()) {
            throw IllegalStateException("Superuzel potřebuje cíl anebo hrany")
        }
        if (finalTargetId != null && currentStateBuilder().moveIds.isNotEmpty()) {
            throw IllegalStateException("Superuzel může mít buď cíl anebo hrany (ne obojí)")
        }
        val states = mutableListOf<State>()
        val moves = mutableListOf<Move>()
        var index = 0
        for (stateBuilder in tmpStates) {
            val nextStateId = "${id}_sub_${index + 1}"
            if (index == 0) {
                stateBuilder.id = id
            } else {
                stateBuilder.id = "${id}_sub_$index"
            }
            val moveId = "h_${id}_sub_$index"
            if (index == tmpStates.size - 1) {
                if (finalTargetId != null) {
                    stateBuilder.moveIds.add(moveId)
                    moves.add(Move(id = moveId, targetId = finalTargetId!!, selfConsuming = selfConsuming))
                }
            } else {
                stateBuilder.moveIds.add(moveId)
                moves.add(Move(id = moveId, targetId = nextStateId, selfConsuming = selfConsuming))

            }
            states.add(stateBuilder.build())
            index += 1
        }
        return Pair(states, moves)
    }

    fun currentStateBuilder(): StateBuilder {
        return tmpStates.last()
    }

    fun addState() {
        tmpStates.add(StateBuilder(chapterId = chapterId, theme = currentTheme))
    }

    fun appendMove(moveId: String) {
        currentStateBuilder().appendMove(moveId)
    }

}

val UZEL = ("uzel=(u[0-9A-Za-z_]*)").toRegex()

fun fixBOM(text: String): String {
    return text.replace("\uFEFF", "")
}

class GameConverter {
    private var currentState: StateBuilder? = null
    private var currentMove: MoveBuilder? = null
    private var currentSuperState: SuperStateBuilder? = null
    private var currentChapter: ChapterBuilder? = null
    private var convertState: ConvertState = ConvertState.NONE
    private var game = GameConfigBuilder()

    internal enum class ConvertState {
        NODE, SUPERNODE, EDGE, EDGE_CONTENT, CHAPTER, NONE
    }

    @JsName("convert")
    fun convert(text: String): GameConfig {
        var index = 0
        for (line in fixBOM(text).split("\n")) {
            try {
                parseLine(line.trim(), index)
            } catch (e: IncorrectLineException) {
                throw e;
            } catch (e: Exception) {
                throw IncorrectLineException(index, line, e = e)
            }
            index += 1
        }
        if (currentState != null) {
            buildState()
        }
        if (currentMove != null) {
            buildMove()
        }
        if (currentSuperState != null) {
            buildSuperState()
        }
        //buildChapter()
        return game.build()
    }

    private val PARAMETER = "([a-z_]+)=(.*)".toRegex()

    private val SEPARATOR = "----"

    private val HRANA = ("hrana=(h[0-9A-Za-z_]*)").toRegex()

    private val KAPITOLA = "kapitola="

    private val ILUSTRACE = "ilustrace=(.*)".toRegex()

    private val POZADI = "pozadi=(.*)".toRegex()

    private val KATEGORIE = "kategorie="

    private val KONEC = "konec=(prohra|nedokonceno)".toRegex()

    private val TEMA = "tema=([a-zA-Z\\-0-9_]+)".toRegex()

    private val SUPERUZEL = "superuzel=(u_[a-zA-Z\\-0-9_]+)".toRegex()

    private val SUPERUZEL_SEPARATOR = "-"

    fun parseLine(line: String, index: Int) {
        if (line.equals(SEPARATOR)) {
        } else if (line.startsWith("#")) {
        } else if (line == "") {
        } else if (line.matches(SUPERUZEL)) {
            val id = line.replace(SUPERUZEL, "$1")
            switchConvertState(ConvertState.SUPERNODE)
            currentSuperState = SuperStateBuilder(id = id, chapterId = currentChapter!!.id)
        } else if (line.startsWith(KAPITOLA)) {
            switchConvertState(ConvertState.CHAPTER)
            val chapterId = line.substring(KAPITOLA.length)
            currentChapter = ChapterBuilder(id = chapterId)
        } else if (line.matches(UZEL)) {
            switchConvertState(ConvertState.NODE)
            if (currentChapter == null) {
                throw IncorrectLineException(msg = "Define chapter before defining state first", line = line, index = index)
            }
            val stateId = parameterValue(line)
            if (game.startId == null) {
                game.startId = stateId
            }
            currentState = StateBuilder(id = stateId, chapterId = currentChapter!!.id, endType = EndType.NONE)
        } else if (line.matches(HRANA)) {
            switchConvertState(ConvertState.EDGE)
            val moveId = parameterValue(line)
            if (currentSuperState == null) {
                currentState!!.appendMove(moveId)
            } else {
                currentSuperState!!.appendMove(moveId)
            }
            currentMove = MoveBuilder(id = moveId)
        } else {
            if (convertState == ConvertState.SUPERNODE) {
                if (SUPERUZEL_SEPARATOR.equals(line)) {
                    currentSuperState!!.addState()
                } else if (line.startsWith(CIL)) {
                    val targetId = line.substring(CIL.length)
                    currentSuperState!!.finalTargetId = targetId
                } else if (line.matches(TEMA)) {
                    val tema = line.replace(TEMA, "$1")
                    currentSuperState!!.currentTheme = tema
                    parseNodeParameters(currentSuperState!!.currentStateBuilder(), line, index)
                } else {
                    parseNodeParameters(currentSuperState!!.currentStateBuilder(), line, index)
                }
            } else if (convertState == ConvertState.NODE) {
                parseNodeParameters(currentState!!, line, index)
            } else if (convertState == ConvertState.EDGE) {
                parseEdgeLine(line, index)
            } else if (convertState == ConvertState.EDGE_CONTENT) {
                errorIfParameter(line, index)
                currentMove!!.appendText(line)
            } else if (convertState == ConvertState.CHAPTER) {
                errorIfParameter(line, index)
                currentChapter!!.name(line)
            } else {
                throw IncorrectLineException(index, line, "ELSE-X")
            }
        }
    }

    fun parseNodeParameters(builder: StateBuilder, line: String, index: Int) {
        if (line.matches(ILUSTRACE)) {
            val illustration = parameterValue(line)
            builder.illustration = illustration
        } else if (line.matches(PRIZNAK)) {
            val priznaky = parameterValue(line)
            val flags = priznaky.split(",").toMutableList()
            currentState!!.flags = flags
        } else if (line.matches(POZADI)) {
            builder.background = parameterValue(line)
        } else if (line.matches(TEMA)) {
            val tema = line.replace(TEMA, "$1")
            builder.theme = tema
        } else if (line.startsWith(CIL)) {
            val targetId = line.substring(CIL.length)
            val moveId = "h_${builder.id}_auto_$targetId"
            builder.moveIds.add(moveId)
            game.moves.add(Move(id = moveId, targetId = targetId, selfConsuming = NODE_SELF_CONSUMING))
        } else if (line.matches(KONEC)) {
            val konec = line.replace(KONEC, "$1")

            builder.endType = when (konec) {
                "prohra" -> EndType.LOSE
                "nedokonceno" -> EndType.UNFINISHED
                "vyhra" -> EndType.WIN
                else -> {
                    throw IncorrectLineException(msg = "Invalid endType: $konec", line = line, index = index)
                }
            }

        } else if (line.startsWith(KATEGORIE)) {
            val categories: Array<String> = line.substring(KATEGORIE.length).split(",").toTypedArray()
            builder.categories = categories
        } else {
            errorIfParameter(line, index)
            builder.appendText(line)
        }
    }

    private fun parameterValue(line: String): String {
        return line.replace(PARAMETER, "$2");
    }

    private fun switchConvertState(newConvertState: ConvertState) {
        if (newConvertState == ConvertState.NODE || newConvertState == ConvertState.SUPERNODE) {
            if (currentSuperState == null) {
                buildState()
            } else {
                buildSuperState()
            }

        } else if (newConvertState == ConvertState.CHAPTER) {
            buildChapter()
        } else if (newConvertState == ConvertState.EDGE) {
            buildMove()
        }
        convertState = newConvertState
    }

    fun buildMove() {
        if (currentMove != null) {
            val move = currentMove!!.build()
            game.moves.add(move)
            currentMove = null
        }
    }

    fun buildChapter() {
        if (currentChapter != null) {
            game.chapters.add(currentChapter!!.build())
        }
    }

    fun buildSuperState() {
        if (currentSuperState != null) {
            val (states, moves) = currentSuperState!!.build()
            game.states.addAll(states)
            game.moves.addAll(moves)
            currentSuperState = null
            if (game.startId == null) {
                game.startId = states[0].id
            }
        }
    }

    fun buildState() {
        if (currentState != null) {
            if (game.startId == null) {
                game.startId = currentState!!.id
            }
            val state = currentState!!.build()
            game.states.add(state)
            currentState = null
        }
    }

    private val PRIZNAK = "priznak=(.*)".toRegex()

    private val VYLOUCENA = "vyloucena="

    private val PODMINENA = "podminena="

    private val POPIS = "popis="

    private val OBRAZEK = "obrazek="

    private val POZIRACI = "poziraci=(ano|ne)".toRegex()

    private val CIL = "cil="

    fun parseEdgeLine(line: String, index: Int) {
        if (line.equals(SEPARATOR)) {
        } else if (line.matches(PRIZNAK)) {
            val priznaky = parameterValue(line)
            val flags = priznaky.split(",").toMutableList()
            currentMove!!.flags = flags
        } else if (line.matches(TEMA)) {
            val tema = line.replace(TEMA, "$1")
            currentMove!!.theme = tema
        } else if (line.matches(POZIRACI)) {
            val selfConsuming = line.replace(POZIRACI, "$1").toLowerCase() == "ano"
            currentMove!!.selfConsuming = selfConsuming
        } else if (line.startsWith(KATEGORIE)) {
            val categories: Array<String> = line.substring(KATEGORIE.length).split(",").toTypedArray()
            currentMove!!.categories = categories
        } else if (line.matches(ILUSTRACE)) {
            val illustration = parameterValue(line)
            currentMove!!.illustration = illustration
        } else if (line.startsWith(OBRAZEK)) {
            val image = line.substring(OBRAZEK.length)
            currentMove!!.image = image
        } else if (line.startsWith(VYLOUCENA)) {
            val flagsDisallowing = line.substring(VYLOUCENA.length).split(",")
            currentMove!!.flagsDisallowing.addAll(flagsDisallowing)
        } else if (line.startsWith(PODMINENA)) {
            val flagsRequiring = line.substring(PODMINENA.length).split(",")
            currentMove!!.flagsRequired.addAll(flagsRequiring)
        } else if (line.startsWith(POPIS)) {
            val caption = line.substring(POPIS.length)
            currentMove!!.caption = caption
        } else if (line.startsWith(CIL)) {
            val targetId = line.substring(CIL.length)
            currentMove!!.targetId = targetId
        } else if (line.equals("-")) {
            switchConvertState(ConvertState.EDGE_CONTENT)
        } else {
            throw IncorrectLineException(index, line, msg = "ELSE")
        }
    }

    fun errorIfParameter(line: String, index: Int) {
        if (line.matches(PARAMETER)) {
            throw IncorrectLineException(index, line, "Neočekávaný parametr")
        }
    }
}

data class IncorrectLineException(val index: Int, val line: String, val msg: String? = "Neočekaváný řádek", val e: Throwable? = null) : Exception(msg
        ?: line, e)
