package eu.karelhovorka.textgame.core.bot

import eu.karelhovorka.textgame.core.Game
import eu.karelhovorka.textgame.core.GameListener
import eu.karelhovorka.textgame.core.bo.EndType
import eu.karelhovorka.textgame.core.bo.Move
import eu.karelhovorka.textgame.core.bo.State
import eu.karelhovorka.textgame.core.bo.isInteractive
import eu.karelhovorka.textgame.core.util.random

class GameCrawlContext(var gameBot: GameBot, originalGame: Game, var move: Move? = null) : GameListener {
    override fun onRestart(game: Game) {
        throw IllegalStateException("bot does not support restarts")
    }

    var game: Game

    val visitedStateIds = mutableMapOf<String, Int>()

    val STATE_LIMIT: Int = 2

    init {
        game = copyGame(originalGame)
    }

    fun copyGame(originalGame: Game): Game {
        return Game(flags = originalGame.flags.toMutableList(), gameConfig = originalGame.gameConfig, listener = this, initialStateId = originalGame.currentState.id, usePause = false)
    }

    override fun onMove(move: Move) {
        println(move.id)
        gameBot.visitedMoves.add(move.id)
    }

    override fun onStart(game: Game) {

    }

    override fun onFinish(game: Game, endType: EndType) {
        if (endType == EndType.NONE) {
            throw IllegalStateException("Illegal end $endType")
        }
        println("finished")
        gameBot.next()
    }

    override fun onState(state: State, moves: Array<Move>) {
        print("${state.id} -> ")
        gameBot.visitedStates.add(state.id)
        val visitCount: Int = visitedStateIds.getOrPut(
                state.id, { 0 }
        )
        if (visitCount > STATE_LIMIT) {
            throw IllegalStateException("Too many visits of a state: ${state.id}")
        }

        if (moves.isInteractive()) {
            for (move in filterUnimportantMoves(moves).dropLast(1)) {
                if (game.flags.contains(move.id)) {
                    throw IllegalStateException("WTF")
                }
                gameBot.cloneFromMove(this, move)
            }
            game.move(moves.random())
        }
    }

    override fun onPause() {
        throw IllegalStateException("Bot should not be used with pause")
    }

    fun filterUnimportantMoves(moves: Array<Move>): Array<Move> {
        if (moves.size == 1) {
            return moves
        }
        val result = mutableListOf<Move>()
        val movesByTarget: Map<String, List<Move>> = moves.groupBy { it.targetId }
        for ((targetId, movesInTarget) in movesByTarget) {
            val unAffectedByConditions: List<Move> = movesInTarget.filter { x -> !gameBot.affectsConditions(x) }
            val affectdByConditions: List<Move> = movesInTarget.filter { x -> gameBot.affectsConditions(x) }
            if (movesInTarget.size == 1 || unAffectedByConditions.isEmpty()) {
                result.addAll(movesInTarget)
            } else {
                if (affectdByConditions.isNotEmpty()) {
                    result.addAll(affectdByConditions)
                }
                if (unAffectedByConditions.isNotEmpty()) {
                    result.add(unAffectedByConditions.first())
                }
            }
        }
        return result.toTypedArray()
    }

    fun start() {
        if (move != null) {
            game.move(move!!.id)
        } else {
            game.start()
        }
    }

}