package eu.karelhovorka.textgame.core.listener

import eu.karelhovorka.textgame.core.bo.GameCompletion
import eu.karelhovorka.textgame.core.bo.Move
import eu.karelhovorka.textgame.core.bo.State
import eu.karelhovorka.textgame.core.saver.GameSaver

class CompletedGameListener(
        val saver: GameSaver,
        val stateIds: MutableSet<String> = mutableSetOf(),
        val moveIds: MutableSet<String> = mutableSetOf()
) : AbstractGameListener() {

    init {
        val completion = saver.loadCompletion()
        stateIds.addAll(completion.stateIds)
        moveIds.addAll(completion.moveIds)
    }

    override fun onMove(move: Move) {
        if (!moveIds.contains(move.id)) {
            moveIds.add(move.id)
            saver.save(GameCompletion(stateIds.toTypedArray(), moveIds.toTypedArray()))
        }
    }

    override fun onState(state: State, moves: Array<Move>) {
        if (!stateIds.contains(state.id)) {
            stateIds.add(state.id)
            saver.save(GameCompletion(stateIds.toTypedArray(), moveIds.toTypedArray()))
        }
    }
}