package eu.karelhovorka.textgame.core.listener

import eu.karelhovorka.textgame.core.Game
import eu.karelhovorka.textgame.core.GameListener
import eu.karelhovorka.textgame.core.bo.EndType
import eu.karelhovorka.textgame.core.bo.Move
import eu.karelhovorka.textgame.core.bo.State


data class ListGameListener(val gameListeners: Array<GameListener>) : GameListener {
    override fun onRestart(game: Game) {
        for (listener in gameListeners) {
            listener.onRestart(game)
        }
    }

    override fun onPause() {
        for (listener in gameListeners) {
            listener.onPause()
        }
    }

    override fun onState(state: State, moves: Array<Move>) {
        for (listener in gameListeners) {
            listener.onState(state, moves)
        }
    }

    override fun onMove(move: Move) {
        for (listener in gameListeners) {
            listener.onMove(move)
        }
    }

    override fun onStart(game: Game) {
        for (listener in gameListeners) {
            listener.onStart(game)
        }
    }

    override fun onFinish(game: Game, endType: EndType) {
        for (listener in gameListeners) {
            listener.onFinish(game, endType)
        }
    }
}
